
const textOfStep=["C","D","E","F","G","A","B"]

class QNote {
	constructor() {
		this.step = 0 //0...6
		this.octave = 4
		this.alter = 0

		//计算出的
		this.line = 0
		this.value = 0
	}

	setNoteValue() {
		//note value
		var val = this.octave*12
		if (this.step<3) {
			val += 2*this.step
		}else{
			//step:3 -> value: 5
			//4 -> 7, 5->9 6->11
			val += 3+2*(this.step-2)
		}
		this.value = val+this.alter
	}
	add(deltaStep, yinshu) {
		var res = new QNote() //{alter: 0}
		res.octave = this.octave
		res.step = this.step+deltaStep
		if (res.step>6) {
			res.step = res.step%7
			res.octave += 1
		}
		res.setNoteValue()
		// console.log(res)
		var newValue = this.value+yinshu
		res.alter = newValue-res.value
		res.value = newValue
		return res
	}
	
	//根据clef, note.step, note.octave, note.alter计算note.accidental
	setNoteValueLineAcc(clef) {
		this.setNoteValue()

		//note line
		var line = this.step
		if (clef === "treble") {
			line -= 6
			line += (this.octave-4)*7
		}else{
			line -= 1
			line += (this.octave-3)*7
		}
		this.line = line

		//note accidental
		if (this.alter === 1) {
			this.accidental = "sharp"
		}else if (this.alter === 2) {
			this.accidental = "sharp-sharp"
		}else if (this.alter === -1) {
			this.accidental = "flat"
		}else if (this.alter === -2) {
			this.accidental = "flat-flat"
		}
	}
	textOfStep() {
		return textOfStep[this.step]
	}
	textOfAlter() {
		if (this.alter === 1) {
			return "#"
		}else if (this.alter === 2) {
			return "x"
		}else if (this.alter === -1) {
			return "b"
		}else if (this.alter === -2) {
			return "bb"
		}
		return ""
	}
	//生成min...max之间的随机整数
	static random(max, min=0) {
		return Math.floor((max-min+1)*Math.random()+min)
	}

	//生成随机note {step: Int, octave: 4, alter: -2...2}
	static randomNote() {
		var note = new QNote()
		note.step = this.random(6) //Math.floor(7*Math.random()) //0...6
		note.alter = this.random(1, -1) //Math.floor(3*Math.random()-1) //-1...1
		note.octave = 4
		note.setNoteValueLineAcc()
		return note
	}
}
export default QNote;



