// 		this.type = "string" //string, score
// 		this.value = ""
// 	}
// }
//题型：
const questionTypes = [
	//音程
	{desc: "以指定音为低音、冠音,构成指定的音程"},
	{desc: "写出下列音程的名称，包括度数和性质"},
	{desc: "将音程转位，并写出转位前、后的音程名称"},

	//和弦
	{desc: "指定根音、三音、五音，构成指定的和弦"},
	{desc: "指定根音、三音、五音，构成原位大、小、增、减三和弦"},
	{desc: "指定根音，构成两种转位三和弦"},
	{desc: "写出下列和弦的名称和根音的音名"},
	{desc: "将和弦转位，并写出转位前、后的和弦名称"},
	{desc: "写出指定大、小调的正三和弦，副三和弦"},

	//调式
	{desc: "写出下列各调的调号"},
	{desc: "写出以下调式的音阶"},
]
/*
question:{
	desc: "",
	score: {notes: []},
	choices: [string]
	answer: {notes: []} or index of choices
}
*/
class Questions {
	constructor() {
		this.questions = []
	}
	//intervals:
	intervalQuestion1(config) {
		var interval = QInterval.randomInterval(config)
		
		var question = {
			desc: "以指定音为低音,构成指定的音程："+interval.name, 
			score: {notes:[interval.root]},
			answer: {note: interval.head}
		}
		this.questions.push(question)
	}
	intervalQuestion2(config) {
		var interval = QInterval.randomInterval(config)
		var question = {
			desc: "以指定音为冠音,构成指定的音程："+interval.name, 
			score: {notes:[interval.head]},
			answer: {note: interval.root}
		}
		this.questions.push(question)
	}
	intervalQuestion3(config) {
		var interval = QInterval.randomInterval(config)
		var choices = [QInterval.randomInterval(config).name, QInterval.randomInterval(config).name, QInterval.randomInterval(config).name]
		var answer = QNote.random(3)
		choices.splice(answer, 0, interval.name)

		var question = {
			desc: "选择音程的名称", 
			score: {notes:[interval.root, interval.head]},
			choices: choices,
			answer: answer
		}
		this.questions.push(question)
	}

	genIntervalQuestion(level) {
		var config = {}
		if (level<3) {
			config.steps = [1,2,3,4,5,6,7,8]
		}
		if (level === 1) {
			config.types = ["小","纯","大"]
		}else if (level === 2) {
			config.types = ["减", "小","纯","大", "增"]
		}
		this.intervalQuestion1(config)
		this.intervalQuestion2(config)
		this.intervalQuestion3(config)
	}
	//chords:
	chordQuestion1(config) {
		var types = ["根音", "三音","五音"]
		for (var i = 0; i < types.length; i++) {
			var chord = QChord.randomChord(config)
			var question = {
				desc: "以指定的音为"+types[i]+"，构成和弦："+chord.name, 
				score: {notes:[chord.notes[i]]},
				answer: {notes: chord.notes}
			}
			this.questions.push(question)
			
		}
	}
	chordQuestion2(config) {
		var chord = QChord.randomChord(config)
		var choices = [QChord.randomChord(config).name, QChord.randomChord(config).name, QChord.randomChord(config).name]
		var answer = QNote.random(3)
		choices.splice(answer, 0, chord.name)

		var question = {
			desc: "选择和弦的名称", 
			score: {notes:chord.notes},
			choices: choices,
			answer: answer
		}
		this.questions.push(question)
	}
	genChordQuestion(level) {
		var config = {}
		if (level === 1) {
			config.types = [3]
		}else if (level === 2) {
			config.types = [3, 7]
		}else if (level === 3) {
			config.types = [3, 7, 9]
		}else {
			config.types = [3, 7, 9, 11]
		}
		this.chordQuestion1(config)
		this.chordQuestion2(config)
	}
	static genQuestions(level) {
		var q = new Questions()
		q.genIntervalQuestion(level)
		q.genChordQuestion(level)
		return q.questions
	}
}

export default Questions;






